.tariff {
  padding-bottom: 100px;

  @include mobile {
    padding-bottom: 50px;
  }

  &__title {
    max-width: 565px;
    margin: auto;
    text-align: center;
  }

  &__descr {
    font-size: 25px;
    color: var(--color-dark-orange);
    text-align: center;
    margin-bottom: 29px;
  }

  &__list {
    margin: 0 76px;
    grid-template-rows: repeat(1, 620px);

    @include tablet {
      grid-template-rows: repeat(1, 480px);
    }

    @include mobile {
      margin: 0 10px;
    }
  }

  &__item {
    grid-column: 4 span;
    background-color: var(--color-snow);
    border-radius: 10px;
    box-shadow: 0px 2px 24px 4px rgba(0, 0, 0, 0.15);
    padding: 20px 23px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__content {
    p {
      color: var(--color-dark-orange);
      font-size: 16px;
    }
  }

  &__name {
    font-size: 23px;
    color: var(--color-dark-orange);
    margin-bottom: 16px;

    span {
      font-weight: 400;
    }
  }

  &__image {
    margin-bottom: 19px;
  }

  &__btn {
    padding: 28px 58px;
    width: 100%;
    filter: drop-shadow(0px 3.5px 3.5px rgba(0, 0, 0, 0.25));
    border-radius: 15px;

    @include tablet {
      margin-top: 15px;
    }
  }

  &__link {
    display: block;
    text-align: center;
    margin-top: 41px;

    a {
      font-size: 25px;
      color: var(--color-dark-orange);
      border-bottom: 1px solid var(--color-dark-orange);
    }
  }
}
