.header {
  position: absolute;
  z-index: 5;
  width: 100%;
  padding-top: 35px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__logo {
    display: flex;
    justify-content: space-between;
    align-items: center;


    span {
      max-width: 232px;
      font-size: 13px;
      color: var(--color-dark-orange);
    }

    img {
      width: 48px;
      height: 39px;
    }

    @include mobile {
      span {
        display: none;
      }
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 10px;

    span {
      max-width: 136px;
      color: var(--color-dark-orange);
      font-size: 15px;
      margin-right: 14px;
    }

    @include tablet {
      span {
        display: none;
      }
    }
  }

  &__item:not(:last-child) {
    margin-right: 9px;
  }

  &__item {
    img {
      transition: transform 0.3s;
    }

    &:hover {
      img {
        transform: translateY(-10px);
        transition: transform 0.3s;
      }
    }
  }

  &__call {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include small-tablet {
      width: 100%;
      margin-top: 15px;
    }

    @include mobile {
      display: none;
    }
  }

  &__phone {
    font-size: 20px;
    font-weight: 700;
    color: var(--color-dark-orange);
  }

  &__mode {
    margin: 0;
    color: var(--color-dark-orange);
    font-size: 12px;
    margin-right: 20px;
  }

  &__btn {
    padding: 9px 14px;
    font-size: 12px;

  }
}
