.presentation {
  padding-bottom: 39px;
  position: relative;
  &__title {
    margin: auto;
    max-width: 660px;
    text-align: center;
  }

  &__descr {
    font-size: 25px;
    color: var(--color-dark-orange) ;
    text-align: center;
    margin-bottom: 43px;

    @include mobile {
      text-shadow: 1px 1px 0px white;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 410px);
    gap: 25px;
    position: relative;

    @include tablet {
      grid-template-columns: repeat(6, 1fr);
    }

    @include small-tablet {
      grid-template-rows: repeat(2, 320px);
    }
  }

  &__item {
    grid-column: 6 span;
    background-image: url("../../img/pres1.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      bottom: 0;
      background: linear-gradient(359.21deg, #1A1A1A 1.21%, rgba(26, 26, 26, 0.4) 99.86%);
    }
  }

  &__content {
    position: relative;
    padding: 130px 120px 40px 120px;
    text-align: center;

    @include tablet {
      padding: 130px 120px;
    }

    @include small-tablet {
      padding: 70px 60px;
    }

    @include mobile {
      padding: 40px 30px;
    }
  }

  &__apart {
    color: var(--color-white);
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 2px;
  }

  &__size {
    color: var(--color-veryLight-orange);
    font-size: 34px;
    font-weight: 700;
  }

  &__link {
    font-size: 20px;
    color: var(--color-white);
    border-bottom: 1px solid var(--color-white);
    margin-top: 112px;
    display: inline-block;
  }

  &__btn {
    display: block;
    text-align: center;
    margin: auto;
    padding: 32px 68px;
    margin-top: 65px;

    @include mobile {
      padding: 30px 45px;
    }
  }

  &__rightPic {
    position: absolute;
    right: 0;
    bottom: -28px;

    @include tablet {
      display: none;
    }
  }
}
