.benefits {
  padding-top: 50px;
  padding-bottom: 50px;

  &__title {
    margin: auto;
    margin-bottom: 56px;
    max-width: 660px;
    text-align: center;
  }
  &__list {
    grid-gap: 50px 80px ;
    grid-template-rows: repeat(3, 170px);

    @include tablet {
      padding: 0px 6%;
    }

    @include mobile {
      padding: 0;
      grid-gap: 40px 55px;
      grid-template-rows: repeat(1, 250px);
    }
  }

  &__item {
    grid-column: 6 span;
    background-color: var(--color-snow);
    display: flex;
    align-items: flex-start;
    padding: 25px 30px;
    box-shadow: 0px 2px 24px 4px rgba(0, 0, 0, 0.15);
    border-radius: 10px;

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__text {
    max-width: 335px;
    margin-top: 6px;
  }

  &__image {
    width: 120px;
    height: 120px;
    border-radius: 100%;
    margin-right: 25px;
    background: linear-gradient(274.27deg, rgba(255, 255, 255, 0) -14.6%, rgba(255, 255, 255, 0.39) 39.52%, rgba(255, 255, 255, 0) 110.6%), linear-gradient(263.81deg, #FFC771 10.32%, #FFC858 98.17%);
    overflow: hidden;
    flex-shrink: 0;

    img {
      height: 100%;
    }
  }

  &__feature {
    font-size: 23px;
    color: var(--color-dark-orange);
    font-weight: 700;
  }

  &__descr {
    font-size: 20px;
    line-height: 24px;
    color: var(--color-dark-orange);
  }

  &__btn {
    display: block;
    text-align: center;
    margin: auto;
    margin-top: 60px;
    padding: 32px 68px;
    border-radius: 10px;

    @include mobile {
      padding: 32px 50px;
    }
  }
}
