.contacts {

  padding: 60px 0;

  &__container {
    background: var(--color-white);
    box-shadow: -4px 4px 50px rgba(143, 143, 143, 0.25);
    border-radius: 5px;
    padding: 50px 60px;
    display: flex;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
    }

    @include mobile {
      padding: 30px 20px;
    }
  }

  &__title {
    max-width: 257px;
    font-weight: 700;
    margin-bottom: 23px;
  }

  &__text {
    margin-right: 15px;

    p {
      color: #FFC858;
      font-size: 25px;
      font-weight: 700;
    }

    a, span {
      font-size: 22px;
      display: block;
      margin-bottom: 30px;
      color: #5A5859;
      font-weight: 700;
      text-decoration: underline;
    }
  }
  &__map {
    width: 640px;
    height: 404px;

    @include tablet {
      width: 450px;
      height: 350px;
    }

    @include small-tablet {
      width: 100%;
    }
  }
}
