@import "mixins";
// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Gilroy", sans-serif;
  --content-width: 1160px; // 1160
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-white: #fff;
  --color-dark-orange: #593F0D;
  --color-light-orange: #835D13;
  --color-veryLight-orange: #ffc771;
  --color-snow: #F9F9F9;


  // шрифты
  --middle-font: 400 40px/48px var(--font-family);
  //--small-font: 400 14px/140% var(--font-family);
}


@include mobile {
  :root {
    --middle-font: 400 32px/38px var(--font-family);
  }
}
