.reviews {

  padding: 60px 0 ;
  &__container {
    position: relative;
  }

  &__title {
    color: var(--color-dark-orange);
    margin: 0 auto;
    margin-bottom: 30px;
    max-width: 460px;
  }
}

.swiper {
  width: 740px;
  height: 410px;
  border-radius: 20px;

  @include small-tablet {
    width: 505px;
    height: 277px;
  }

  @include mobile {
    width: 270px;
    height: 140px;
  }
}

.slide {
  &--one {
    //background-image: url("../../img/slide1.jpg");
    position: relative;

    video {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    img {
      position: absolute;
      left: 0;
      top: 0;
    }
    &__play {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 120px;
      height: 120px;
      background-image: url("../../img/play.png");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      z-index: 50;

      @include mobile {
        width: 60px;
        height: 60px;
      }
    }
  }

  &--two {
    background-image: url("../../img/slide1.jpg");
  }

  &--three {
    background-image: url("../../img/slide1.jpg");
  }
}

.swiper-button-next, .swiper-button-prev {
  color: var(--color-dark-orange);
  width: 182px;
  height: 124px;
  margin: auto;

  @include small-tablet {
    width: 50px;
    height: 96px;
  }
}

.swiper-button-next:after, .swiper-button-prev:after {
  font-size: 66px;
}

.swiper-slide {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
