.brands {
  padding: 100px 0;
  position: relative;

  @include tablet {
    padding: 0 0 50px;
  }
  &__container {
    text-align: center;
  }
  &__title {
    max-width: 740px;
    margin: 0 auto;
    margin-bottom: 7px;
  }

  &__descr {
    font-size: 25px;
    color: var(--color-dark-orange);
    margin-bottom: 17px;
  }

  &__image {
    margin-bottom: 57px;
  }

  &__btn {
    display: block;
    margin: auto;
    padding: 32px 68px;
  }

  &__bottom {
    position: absolute;
    bottom: 0;
    left: 0;

    @include small-tablet {
      display: none;
    }
  }

  &__bottom2 {
    position: absolute;
    bottom: 112px;
    right: 107px;

    @include small-tablet {
      display: none;
    }
  }
}
