.payment {
padding: 52px 0 30px 0;
  &__title {
    max-width: 483px;
    margin: 0 auto;
    margin-bottom: 47px;
  }

  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 735px;
    margin: auto;
  }

  &__descr {
    font-size: 25px;
    color: var(--color-dark-orange);
    margin-bottom: 30px;
  }

  &__image {
    flex-shrink: 0;

    @include mobile {
      display: none;
    }
  }

  &__btn {
    padding: 32px 72px;

    @include small-tablet {
      padding: 20px 60px;
    }
  }
}
