.hero {
  background-image: url("../../img/hero_bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 650px;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    background: linear-gradient(90deg, #FFFFFF 20%, rgba(255, 255, 255, 0) 86.67%);
  }

  &__container {
    position: relative;
    padding: 142px 142px 123px 100px;

    @include small-tablet {
      padding: 140px 70px 100px;
    }

    @include mobile {
      padding: 100px 20px;
    }
  }


  &__title {
    margin: 0;
    //font: var(--middle-font);
    //color: var(--color-dark-orange);
    position: relative;
    margin-bottom: 59px;
    max-width: 580px;

    &:after {
      content: "";
      border-bottom: 3px solid #FFCF6E;
      position: absolute;
      width: 100px;
      left: 0;
      bottom: -33px;
    }
  }

  &__descr {
    margin: 0;
    font-size: 25px;
    color: var(--color-dark-orange);
    margin-bottom: 39px;
    max-width: 403px;
  }

  &__smeta {
    position: relative;
  }

  &__btn {
    padding: 32px 40px;
    font-weight: 700;
    font-size: 12px;
  }

  &__image {
    position: absolute;
    top: -32px;
    left: 198px;
  }

  &__bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
}
