.calculate {

  &__title {
    max-width: 660px;
    text-align: center;
    margin: auto;
  }

  &__inner {
    display: flex;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__price {
    width: 50%;
    padding: 40px;
    padding-top: 0;

    @include small-tablet {
      width: 100%;
    }

    @include mobile {
      padding: 15px;
    }
  }

  &__image {
    width: 50%;
    max-height: 785px;
    overflow: hidden;

    @include small-tablet {
      display: none
    }
  }
}

.price {

  select {
    width: 100%;
    padding: 20px;
    appearance: none;
    overflow: hidden;
    background: url('../../img/Rectangle.png') no-repeat right;
    background-size: 12px 10px;
  }

  option {
    font-size: 15px;
    padding: 10px;
  }

  &__name {
    input {
      width: 100%;
      padding: 20px;
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 10px;
    }
  }

  &__type {
    margin-bottom: 30px;

    p {
      margin-bottom: 10px;
    }
  }

  &__tariff {
    margin-bottom: 30px;

    p {
      margin-bottom: 10px;
    }
  }

  &__square {
    p {
      margin-bottom: 10px;
    }

    input {
      text-align: center;
      width: 50px;
      height: 40px;
      margin-bottom: 10px;
      border-radius: 5px;
    }

    strong {
      display: block;
      margin-bottom: 30px;
    }
  }

  &__name {

  }

  &__btn {
    padding: 20px 25px;
    min-width: 380px;

    @include small-tablet {
      min-width: 0;
      width: 100%;
    }
  }

  .noUi-connect {
    background: var(--color-white);
  }

  .noUi-horizontal {
    height: 10px;
  }

  .noUi-handle-upper {
    display: none;
  }

  .noUi-target {
    background: var(--color-veryLight-orange);
  }

  .noUi-horizontal .noUi-handle {
    width: 20px;
    height: 20px;
    right: -14px;
    top: -7px;
    border-radius: 100%;
  }

  .noUi-handle:after, .noUi-handle:before {
    display: none;
  }
}

.slider {
  &__rang {
    display: flex;
    justify-content: space-between;
  }
}

.noUi-touch-area {
  cursor: pointer;
}
