.sample {
  position: relative;
  background-image: url("../../img/sample_bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 797px;

  &__top {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  &__middle {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);

    @include small-tablet {
      display: none;
    }
  }

  &__shadow {
    position: absolute;
    bottom: 0;
    left: 187px;
    z-index: 2;

    @include tablet {
      display: none;
    }
  }

  &__bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  &__container {
    display: flex;
    justify-content: space-around;
    //align-items: center;
    padding: 70px 30px;
    z-index: 100;
    position: relative;

    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__title {
    font-size: 30px;
    color: var(--color-dark-orange);
    margin-bottom: 20px;
  }

  &__descr {
    font-size: 20px;
    color: var(--color-dark-orange);
    max-width: 300px;
  }

  &__list {
    font-size: 20px;
    max-width: 313px;
  }

  &__item {
    color: var(--color-dark-orange);
    margin-bottom: 6px;

    &::marker {
      color: var(--color-veryLight-orange);
    }
  }

  &__left {
    max-width: 413px;
    margin-top: 30px;
  }

  &__right {
    background-color: var(--color-white);
    border-radius: 10px;
    max-width: 400px;
    z-index: 2;

    @include mobile {
      max-width: none;
      width: 100%;
    }
  }
}


.form--sample {
  padding: 32px 30px 54px 30px;

  @include mobile {
    padding: 32px 15px;
  }
  &__contact {
    font-size: 28px;
    color: var(--color-dark-orange);
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__call {
    font-size: 15px;
    color: #373737;
    margin-bottom: 10px;
    max-width: 294px;
  }

  &__input {
    width: 317px;
    height: 58px;
    border: none;
    border-bottom: 1px solid #D9D9D9;
    padding: 32px 0;

    @include mobile {
      width: 100%;
    }

    //&:first-of-type {
    //  margin-bottom: 35px;
    //}
  }

  &__btn {
    font-size: 12px;
    color: #835D13;
    padding: 32px 56px;
    margin: auto;
    margin-top: 54px;
    display: block;
    text-align: center;
    font-weight: 700;

    @include mobile {
      padding: 25px 20px;
    }
  }
}


.just-validate-error-label {
  text-align: center;
}
