.instruction {
  padding: 74px 0 113px 0;
  position: relative;


  &__bottom {
    position: absolute;
    right: 95px;
    bottom: -7px;

    @include tablet {
      display: none;
    }
  }

  &__title {
    max-width: 660px;
    text-align: center;
    margin: auto;
    margin-bottom: 66px;
  }



  &__list {
    display: flex;
    padding-left: 214px;
    flex-direction: column;
    align-items: self-start;

    @include tablet {
      padding-left: 10%;
    }

    @include mobile {
      padding-left: inherit;
    }
  }

  &__item {
    display: flex;
    align-items: center;

    &:not(:first-child) {
      max-width: 500px;
    }

    &:not(:last-child) {
      margin-bottom: 60px;
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__num {
    width: 74px;
    height: 74px;
    border-radius: 100%;
    background-color: #FBD293;
    position: relative;
    margin-right: 26px;
    flex-shrink: 0;

    p {
      position: absolute;
      font-size: 30px;
      font-weight: 700;
      color: var(--color-dark-orange);
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__subtitle {
    font-size: 23px;
    font-weight: 700;
    color: var(--color-dark-orange);
  }

  &__descr {
    font-size: 19px;
    color: var(--color-dark-orange);
    max-width: 630px;
  }

  &__btn {
    display: block;
    text-align: center;
    margin: auto;
    margin-top: 68px;
    padding: 32px 68px;
  }
}
